<template>
  <div class="AddInput" @mouseover='open' @mouseout='close'>
    <img src="../images/component/AddInput/add.png" v-show="!InputHand">
    <div class="AddInput__form" v-show="InputHand">
      <div class="AddInput__form__select">
        <dropdown style="margin-bottom: 20px;" title="学校名称 / School Name" :option='schoolList' placeholder='请选择学校'></dropdown>
        <dropdown title="专业" :option='zhanList' placeholder='专业' icon></dropdown>
      </div>
    </div>
  </div>
</template>

<script>
import dropdown from './dropdown'
export default {
  name: 'AddInput',
  data () {
    return {
      InputHand: false,
      schoolList: ['纽约大学(NewYork Uni..)', '纽约大学(NewYork Uni..)', '纽约大学(NewYork Uni..)', '纽约大学(NewYork Uni..)', '查看更多(more)'],
      zhanList: ['经济', '管理', '会计', '金融', '数学', '统计', '电子电气', '通信工程', '计算机', '传播学', '艺术', '体育']
    }
  },
  components: {
    dropdown
  },
  methods: {
    open () {
      this.InputHand = true
    },
    close () {
      this.InputHand = false
    }
  }

}
</script>

<style scoped>
.AddInput{
  width: 210px;
  height: 210px;
}
.AddInput img{
  width: 100%;
  height: 100%;
}
.AddInput__form{
  width: 100%;
  height: 100%;
  background-color: rgba(177, 218, 217, 0.08)
}
.AddInput__form__select{
  width: 90%;
  height: auto;
  padding: 20px 10px;
}
</style>



