<template>
  <div>
    <button class="button" type="button"><slot></slot></button>
  </div>
</template>

<script>
export default {
  name: 'Button'
}
</script>

<style scoped>

.button{
  width: 110px;
  height: 45px;
  outline:none;
  cursor: pointer;
  border: 2px solid rgba(177, 218, 217, 0.3);
  background-color: rgba(177, 218, 217, 0.08);
  color: rgba(177, 218, 217,0.8);
}
.button:hover{
  background-color: rgba(177, 218, 217, 0.3);
  color: rgb(177, 218, 217);
}
</style>



