<template>
  <div class="dropdown">
    <div class="dropdown__title">{{ title }}</div>
      <button class="dropdown__button" @click='toggleList'>
        <span v-show="!hasPlaceholder">{{ getValue }}</span>
        <span v-show="hasPlaceholder">{{ placeholder }}</span>
        <div class="dropdown__icon" v-if="icon">
          <img src="../images/component/dropdown/pull-up.png" v-show="!ClickDropdown">
          <img src="../images/component/dropdown/pull-down.png" v-show="ClickDropdown">
        </div>
      </button>
      <ul class="dropdown__list scrollbar" v-show="ClickDropdown" >
        <li v-for="(item,index) in option" @click='select(index)'> {{ item }}</li>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'dropdown',
  props: {
    icon: Boolean,
    title: String,
    option: Array,
    placeholder: String
  },
  data () {
    return {
      ClickDropdown: false,
      getValue: '',
      hasPlaceholder: true
    }
  },
  methods: {
    toggleList () {
      this.ClickDropdown = !this.ClickDropdown
    },
    select (index) {
      this.ClickDropdown = false
      this.getValue = this.option[index]
      this.hasPlaceholder = false
      this.$emit('select')
    }
  }
}
</script>

<style scoped>
.dropdown__title{
  color: rgb(177, 218, 217);
  font-size: 12px;
  margin-bottom: 12px;
}
.dropdown__button{
  outline:none;
  border: none;
  font-size: 12px;
  cursor: pointer;
  height: 30px;
  width: auto;
  line-height: 30px;
  background-color: rgba(177, 218, 217, 0.08);
  color: rgba(177, 218, 217,0.5);
}
.dropdown__button span{
  padding-left: 6px;
  padding-right: 6px;
}
.dropdown__icon{
  float: right;
}
.dropdown__button img{
  padding: 12px;
}
.dropdown__list{
  position: absolute;
  z-index: 99;
  max-height: 200px;
  overflow: auto;

}
.dropdown__list li{
  list-style: none;
  font-size: 12px;
  cursor: pointer;
  height: 30px;
  line-height: 30px;
  padding-left: 5px;
  padding-right: 42px;
  padding-top: 5px;
  padding-bottom: 5px;
  outline:none;
  background-color: #34597B;
  color: rgba(177, 218, 217,0.5);
}
.dropdown__list li:hover{
  background-color: #418196;
  color: #82FCFF;
}



/*滚动条基本大小和颜色*/
.scrollbar::-webkit-scrollbar{
    width: 8px;
    height: 8px;
    background-color: #34597B;
}
/*定义滚动条的轨道，内阴影及圆角*/
.scrollbar::-webkit-scrollbar-track{

    border-radius: 50%;
    background-color: #34597B;
}
/*定义滑块，内阴影及圆角*/
.scrollbar::-webkit-scrollbar-thumb{
    /*width: 10px;*/
    height: 20px;
    border-radius: 10px;
    background-color: rgba(177, 218, 217,0.3);
}
</style>



